I,mBAAA;EA5Rd;EA+RU;IACI,mBAAA;EA7Rd;EAgSU;IACI,mBAAA;EA9Rd;;EAoSM;IACI,SAAA;IACA,kBAAA;EAjSV;EAmSU;IACI,mBAAA;IACA,mBAAA;IACA,aAAA;IACA,mBAAA;EAjSd;EAoSkB;IACI,iBAAA;EAlStB;EAqSkB;IACI,mBAAA;EAnStB;EAwSU;IACI,mBAAA;IACA,4BAAA;IACA,aAAA;EAtSd;EAySU;IACI,mBAAA;EAvSd;EA0SU;IACI,mBAAA;EAxSd;EA2SU;IACI,mBAAA;EAzSd;EA4SU;IACI,mBAAA;EA1Sd;EA4Sc;IACI,WAAA;IACA,YAAA;EA1SlB;;EAiTM;IACI,SAAA;IACA,kBAAA;EA9SV;EAgTU;IACI,mBAAA;EA9Sd;EAiTU;IACI,mBAAA;EA/Sd;EAkTU;IACI,mBAAA;EAhTd;EAmTU;IACI,mBAAA;EAjTd;EAqTM;IACI,qCAAA;IACA,SAAA;IACA,kBAAA;IACA,kBAAA;IACA,aAAA;IACA,mBAAA;IACA,4BAAA;EAnTV;EAqTU;IACI,kBAAA;EAnTd;EAsTU;IACI,kBAAA;EApTd;EAuTU;IACI,kBAAA;EArTd;EAwTU;IACI,kBAAA;EAtTd;EAyTU;IACI,WAAA;IACA,YAAA;EAvTd;AACF","sourcesContent":["// ------------------------------ Fonts ------------------------------\r\n\r\n@font-face {\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n    src: url(\"https://use.typekit.net/axk6esy.css\");\r\n}\r\n\r\n@font-face {\r\n    font-family: forma-djr-micro, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    src: url(\"https://use.typekit.net/axk6esy.css\");\r\n}\r\n\r\n// ------------------------------ Variables ------------------------------\r\n\r\n:root {\r\n    // Colors\r\n    --purple-dark: #780CD0;\r\n    --purple: #B361F6;\r\n    --purple-light: #CE99F9;\r\n\r\n    --blue-dark: #0C23D0;\r\n    --blue: #6071F6;\r\n    --blue-light: #99A4F9;\r\n\r\n    --accent: #F6B260;\r\n    --grey: #C9D4DA;\r\n    --off-white: #FBFBFB;\r\n    --white: #FFFFFF;\r\n\r\n    --heading: #0A0111;\r\n    --text: #666666;\r\n\r\n    // Font type\r\n    --body-font: 'haboro-contrast-normal, sans-serif';\r\n    --heading-font: 'forma-djr-micro, sans-serif';\r\n\r\n    // Font size\r\n    --font-size: 20px;\r\n    --font-size-med: 18px;\r\n    --font-size-sml: 16px;\r\n\r\n    // Shadows\r\n    --main-shadow:\r\n        0 4px 24px 0 rgb(103 151 255 / 10%), 0 12px 64px 0 rgb(103 151 255 / 10%);\r\n    --hover-shadow:\r\n        0 4px 24px 0 rgb(103 151 255 / 25%), 0 12px 64px 0 rgb(103 151 255 / 25%);\r\n\r\n    // Animation\r\n    --animate-duration: 1s;\r\n    --animate-delay: 1s;\r\n    --animate-repeat: 1;\r\n}\r\n\r\n*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n    text-decoration: none;\r\n    list-style: none;\r\n    margin-top: 0;\r\n    margin-left: 0;\r\n    margin-right: 0;\r\n    padding: 0;\r\n}\r\n\r\nhtml {\r\n    box-sizing: inherit;\r\n    scroll-behavior: smooth;\r\n    margin: auto;\r\n\r\n}\r\n\r\nbody {\r\n    background-color: var(--off-white);\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n    font-size: var(--font-size);\r\n    color: var(--text);\r\n    line-height: 1.6;\r\n    user-select: none;\r\n    margin: auto;\r\n}\r\n\r\n// ------------------------------ Fonts ------------------------------\r\n\r\nh1,\r\nh2,\r\nh3,\r\nh4 {\r\n    font-family: forma-djr-micro, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    line-height: 1.1;\r\n    color: var(--heading);\r\n}\r\n\r\nh1 {\r\n    font-size: 4.209rem;\r\n}\r\n\r\nh2 {\r\n    font-size: 3.157rem;\r\n}\r\n\r\nh3 {\r\n    font-size: 2.369rem;\r\n}\r\n\r\nh4 {\r\n    font-size: 1.777rem;\r\n}\r\n\r\np {\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 300;\r\n    font-style: normal;\r\n}\r\n\r\n.bold_paragraph {\r\n    color: var(--white);\r\n    font-weight: 700;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.bold_paragraph_dark {\r\n    color: var(--heading);\r\n    font-weight: 700;\r\n}\r\n\r\n// ------------------------------ Buttons ------------------------------\r\n\r\nbutton {\r\n    text-transform: uppercase;\r\n    font-family: haboro-contrast-normal, sans-serif;\r\n    font-weight: 500;\r\n    font-style: normal;\r\n    font-size: var(--font-size);\r\n    letter-spacing: 3px;\r\n    color: var(--white);\r\n    padding: 15px 40px;\r\n    border-radius: 10px;\r\n    border-width: 0px;\r\n    box-shadow: var(--main-shadow);\r\n    transition: all 0.3s ease-in-out;\r\n}\r\n\r\nbutton:hover {\r\n    box-shadow: var(--hover-shadow);\r\n}\r\n\r\n.button_purple {\r\n    background-color: var(--purple);\r\n\r\n    &:hover {\r\n        background-color: rgba(179, 97, 246, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_black {\r\n    background-color: var(--heading);\r\n\r\n    &:hover {\r\n        background-color: rgba(0, 0, 0, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_grey {\r\n    background-color: var(--grey);\r\n    color: var(--heading);\r\n\r\n    &:hover {\r\n        background-color: rgba(201, 212, 218, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.button_blue {\r\n    background-color: var(--blue-light);\r\n    color: var(--white);\r\n\r\n    &:hover {\r\n        background-color: rgba(153, 164, 249, 0.8);\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n// ------------------------------ Sections ------------------------------\r\n\r\nsection {\r\n    width: 100%;\r\n    margin: auto;\r\n    padding: 0 120px;\r\n}\r\n\r\n.wrapper {\r\n    display: grid;\r\n    grid-template-columns: repeat(12, 1fr);\r\n    max-width: 1640px;\r\n    width: 100%;\r\n    padding: 150px 0;\r\n    margin: 0;\r\n\r\n    .heading {\r\n        grid-area: 1 / 2 / 2 / 12;\r\n        grid-template-rows: repeat(1, 1fr);\r\n        text-align: center;\r\n    }\r\n}\r\n\r\n// ------------------------------ Navbar ------------------------------\r\n\r\n#navbar {\r\n    background: url(../wave.svg);\r\n    background-repeat: no-repeat;\r\n    background-position: center;\r\n    background-size: cover;\r\n    width: 100%;\r\n    height: 200px;\r\n    box-shadow: var(--main-shadow);\r\n}\r\n\r\n.logo {\r\n    width: 60px;\r\n    height: auto;\r\n\r\n    &:hover {\r\n        cursor: pointer;\r\n    }\r\n}\r\n\r\n.contactNo {\r\n    align-items: center;\r\n    justify-content: center;\r\n    transform: translateY(10px);\r\n    text-align: center;\r\n    color: var(--white);\r\n\r\n    p {\r\n        font-family: haboro-contrast-extended, sans-serif;\r\n        font-weight: 700;\r\n        font-style: normal;\r\n        font-size: var(--font-size);\r\n    }\r\n}\r\n\r\n.menu {\r\n    position: fixed;\r\n    width: calc(100% - 260px);\r\n    top: 50px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 10px 20px;\r\n    border-radius: 20px;\r\n    background-color: rgba(255, 255, 255, 0.8);\r\n    box-shadow: var(--main-shadow);\r\n    z-index: 10;\r\n}\r\n\r\n.menu_bottom {\r\n    position: fixed;\r\n    top: 158px;\r\n    left: 0;\r\n    width: 100%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-family: haboro-contrast-extended, sans-serif;\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    font-size: var(--font-size);\r\n    word-spacing: 15px;\r\n    text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.25);\r\n\r\n    a {\r\n        color: var(--heading);\r\n        transition: all 0.3s ease-in-out;\r\n\r\n        &:hover {\r\n            color: var(--blue);\r\n        }\r\n    }\r\n}\r\n\r\n// ------------------------------ Section A ------------------------------\r\n\r\n#section-a {\r\n    background-image: url(../asset_1.svg);\r\n    background-repeat: no-repeat;\r\n    background-position: bottom;\r\n    background-size: cover;\r\n    height: 100%;\r\n    color: var(--white);\r\n\r\n    .wrapper {\r\n        .heading {\r\n            grid-area: 1 / 3 / 1 / 11;\r\n            display: flex;\r\n            flex-direction: column;\r\n        }\r\n    }\r\n\r\n    h1,\r\n    h3,\r\n    p {\r\n        color: var(--heading);\r\n    }\r\n}\r\n\r\nbutton {\r\n    margin: 10px;\r\n}\r\n\r\n// ------------------------------ Section B ------------------------------\r\n\r\n#section-b {\r\n    .wrapper {\r\n        .heading {\r\n            grid-area: 1 / 